* {
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  background: linear-gradient(90deg, rgba(4,1,61,1) 0%, rgba(37,37,157,1) 37%, rgba(0,212,255,1) 100%);;
}
.fetch_button {
  height: 50px;
  width: 100px;
  border-radius: 10px;
  cursor: pointer;
  background: linear-gradient(90deg, rgba(255,5,249,1) 0%, rgba(252,151,255,1) 49%, rgba(255,0,236,1) 100%);
  border: 1px solid black;
}
.fetch_button:focus {
  outline: none;
}
.image_dog {
  width: 400px;
  height: 400px;
  border: 10px solid #fff;
}
.block, .block_2 {
  width: 30%;
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
}
.block_2 {
  justify-content: left;
}
